.dropDown-enter {
    transform: translateY(-102%);
}

.dropDown-enter-active {
    transition: 350ms ease-in;
    transform: translateY(0%);
}

.dropDown-exit {

}

.dropDown-exit-active {
    transition: 250ms ease-out;
    transform: translateY(-102%);
}
/*-------------------------------------------------------------------------------------*/
.arrow-enter {

}

.arrow-enter-active {
    transition: 350ms ease-in;
    transform: rotate(180deg);
}

.arrow-exit {

}

.arrow-exit-active {
    transition: 250ms ease-out;
    transform: rotate(0);
}
/*-------------------------------------------------------------------------------------*/
