.container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-items: center;
    font-size: calc(8px + 3vmin);
    font-weight: 400;
    transition: 0.5s;
}

.topContainer {
    border: 2px solid slategrey;
    padding: 0 5%;
    border-radius: 15px;
    position: relative;
}

.animateWrapper {
    overflow: hidden;
}
