.list {
    padding: 0;
    list-style: none;
    margin: 1% 0 1% 0;
    border: 1px solid slategrey;
    border-radius: 15px;
}

.listIcon {
    font: inherit;
    float: right;
    cursor: pointer;
    margin-right: 2%;
}

.listItem {
    transition: 0.35s;
    border-bottom: 1px solid slategrey;
}

.listItem:last-child {
    border-bottom: none;
}

.listItem:hover {
    border-radius: 15px;
    background-color: navajowhite;
}

.listItemBtn {
    padding-left: 30%;
    padding-right: 0;
    border: none;
    background-color: transparent;
    cursor: pointer;
    font-size: calc(10px + 3vmin);
    font-weight: 600;
}

.topContainer h1 {
    text-align: center;
    margin: 4% 0 0 0;
    cursor: pointer;
}

.cityArrow {
    font: inherit;
    font-size: 40%;
    position: absolute;
    margin-top: 9%;
    transform: rotate(90deg);
}

.reversed {
    transform: rotate(180deg);
}

